@font-face {
  font-family: 'Nunito Black';
  src: local("Nunito Black"), url("../fonts/Nunito-Black.ttf"); }

@font-face {
  font-family: 'Nunito Bold';
  src: local("Nunito Bold"), url("../fonts/Nunito-Bold.ttf"); }

@font-face {
  font-family: 'Nunito ExtraBold';
  src: local("Nunito ExtraBold"), url("../fonts/Nunito-ExtraBold.ttf"); }

@font-face {
  font-family: 'Nunito SemiBold';
  src: local("Nunito SemiBold"), url("../fonts/Nunito-SemiBold.ttf"); }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0; }

/* containers */
.grid {
  display: grid;
  justify-content: center;
  align-items: center; }

@screen xl {
  .grid {
    @apply gap-30px grid-cols-xl ml-15px mr-15px; } }

@screen lg {
  .grid {
    @apply gap-30px grid-cols-lg ml-15px mr-15px; } }

@screen md {
  .grid {
    @apply gap-30px grid-cols-lg ml-15px mr-15px; } }

@screen sm {
  .grid {
    @apply gap-30px grid-cols-md ml-15px mr-15px; } }

@screen xs {
  .grid {
    @apply gap-24px grid-cols-sm ml-12px mr-12px; } }

.box {
  border: 1px solid theme("colors.tertiary");
  @apply w-full px-24px;
  height: auto;
  box-shadow: 0px 2px 4px rgba(6, 115, 123, 0.04);
  border-radius: 8px; }

.marg {
  @apply block w-full; }

.marg-XL {
  height: 68px;
  padding: 0; }

.marg-L {
  height: 40px;
  padding: 0; }

.marg-M {
  height: 32px;
  padding: 0; }

.marg-S {
  height: 24px;
  padding: 0; }

/* fonts */
