  font-family: "Nunito Black";
  font-size: 36px;
  line-height: 48px;
  letter-spacing: -0.6px; }

.Hero {
  font-family: "Nunito Bold";
  font-size: 32px;
  line-height: 42px;
  letter-spacing: -0.8px; }

.H1 {
  font-family: "Nunito ExtraBold";
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.4px; }

.H2 {
  font-family: "Nunito Bold";
  font-size: 22px;
  line-height: 28px;
  letter-spacing: -0.4px; }

.H3 {
  font-family: "Nunito Bold";
  font-size: 18px;
  line-height: 26px;
  letter-spacing: -0.1px; }

.H4 {
  font-family: "Nunito ExtraBold";
