  line-height: 20px;
  letter-spacing: 0px; }

.label {
  font-family: "Nunito Bold";
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0px; }

.headbar-text_mobile {
  @apply text-content-grey mark; }

@screen sm {
  .headbar-text_mobile {
    font-family: "Nunito ExtraBold";
    font-size: 8px;
    line-height: 11px;
    letter-spacing: 4px;
    text-transform: uppercase; } }

@screen xs {
  .headbar-text_mobile {
    font-family: "Nunito ExtraBold";
    font-size: 8px;
    line-height: 11px;
    letter-spacing: 4px;
    text-transform: uppercase; } }

/* icons */
.iconRound16 {
  @apply h-16px w-16px;
  font-size: 10px; }

.iconRound20 {
  @apply h-20px w-20px;
  font-size: 12px; }

.iconRound24 {
  @apply h-24px w-24px;
  font-size: 14px; }

.iconRound32 {
  @apply h-32px w-32px;
  font-size: 24px; }

/* buttons */
.fit-content {
  width: fit-content; }

.btn-large {
  height: 56px;
  padding: 16px 32px;
  box-shadow: 0px 4px 7px rgba(31, 174, 185, 0.05);
  border-radius: 55px;
  font-family: "Nunito Black";
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 0px; }
  .btn-large:disabled {
    @apply bg-content-grey_light text-content-white;
    opacity: 0.5; }
  .btn-large:focus {
    outline: none; }
  .btn-large span {
    @apply iconRound20; }

.btn-medium {
  height: 40px;
  padding: 12px 16px;
  box-shadow: 0px 4px 7px rgba(31, 174, 185, 0.05);
  border-radius: 55px;
  font-family: "Nunito Black";
  font-size: 12px;
  line-height: 16px;
  letter-spacing: -0.2px; }
  .btn-medium:disabled {
    @apply bg-content-grey_light text-content-white;
    opacity: 0.5; }
  .btn-medium:focus {
    outline: none; }
  .btn-medium span {
    @apply iconRound16; }

.btn-small {
  height: 24px;
  padding: 8px 10px;
  box-shadow: 0px 4px 7px rgba(31, 174, 185, 0.05);
  border-radius: 55px;
  font-family: "Nunito Black";
  font-size: 10px;
  line-height: 8px;
  letter-spacing: 0px; }
  .btn-small:disabled {
    @apply bg-content-grey_light text-content-white;
    opacity: 0.5; }
  .btn-small:focus {
    outline: none; }

.btn-primary {
  @apply bg-brand-primary text-content-white; }
  .btn-primary span {
    @apply bg-content-white text-brand-primary; }

.btn-secondary {
  @apply bg-brand-secondary text-content-white; }
  .btn-secondary span {
    @apply bg-content-white text-brand-secondary; }

.btn-white {
  @apply bg-content-white text-content-dark; }
  .btn-white span {
    @apply bg-content-grey_ultra_light text-content-dark; }

/* divider */
.divider {
  height: 1.5px;
  background: linear-gradient(0.25turn, rgba(135, 149, 179, 0.4), theme("colors.content.grey_ultra_light")); }

@screen xl {
  .divider {
    @apply col-span-12; } }

@screen lg {
  .divider {
    @apply col-span-12; } }

@screen md {
  .divider {
    @apply col-span-12; } }

@screen sm {
  .divider {
    @apply col-span-6; } }

@screen xs {
  .divider {
    @apply col-span-4; } }

/* input */
.input {
  height: 56px;
  box-shadow: 0px 2px 4px rgba(6, 115, 123, 0.04);
  border-radius: 8px;
  border: 1px solid theme("colors.tertiary"); }
  .input:focus {
    box-shadow: inset 0 0 0 2px theme("colors.content.dark");
    outline: none; }
  .input::placeholder {
    @apply text-content-grey_light; }

.input:focus + .input_icon {
  @apply text-content-dark; }

.input:active + .input_icon {
  @apply text-content-dark; }

.input_icon {
  @apply text-content-grey_light; }

.warning {
  box-shadow: inset 0 0 0 2px theme("colors.status.alert") !important; }

.error {
  box-shadow: inset 0 0 0 2px theme("colors.status.error") !important; }

.input_icon {
  right: 24px;
  bottom: 28px; }

.alert {
  @apply w-full fixed left-0 z-50 px-24px py-15px text-center H3 text-content-white flex items-center justify-center; }

.slideIn {
  animation-name: slidein;
  animation-duration: 2s;
  @apply bottom-0; }

.slideOut {
  animation-name: slideout;
  animation-duration: 3s;
  bottom: -200px; }

@keyframes slidein {
  from {
    bottom: -200px;
    margin-bottom: -60px; }
  to {
    bottom: 0;
    margin-bottom: 0; } }

@keyframes slideout {
  from {
    bottom: 0;
    margin-bottom: 0; }
  to {
    bottom: -200px;
    margin-bottom: -60px; } }

/* checkbox */
.checkbox-input {
  @apply hidden; }

.checkbox-input:checked + .checkbox {
  @apply bg-content-dark;
  border: 2px solid theme("colors.content.dark"); }

.checkbox {
  @apply bg-content-white cursor-pointer;
  width: 24px;
  height: 24px;
  border-radius: 5px;
  border: 2px solid theme("colors.content.grey_light"); }

/* radio */
.radio {
  @apply cursor-pointer;
  width: 24px;
  height: 24px;
  border-radius: 12px;
  border: 2px solid theme("colors.content.grey_light"); }

.radio_input:checked + .radio {
  border: 2px solid theme("colors.content.dark"); }
  .radio_input:checked + .radio .circle_inner {
    width: 10px;
    height: 10px;
    border-radius: 10px;
    @apply bg-content-dark; }

/* toggle */
.toggle {
  @apply flex items-center justify-between cursor-pointer bg-status-alert relative;
  width: 40px;
  height: 24px;
  border-radius: 20px;
  transition: background-color .2s; }

.toggle_dark {
  @apply bg-content-grey_light; }

.toggle .switch_btn {
  @apply absolute bg-content-white flex items-center justify-center;
  top: 2px;
  left: 2px;
  width: 20px;
  height: 20px;
  border-radius: 20px;
  transition: 0.2s;
  box-shadow: 0 0 2px 0 rgba(10, 10, 10, 0.29); }

.toggle_input:checked + .toggle {
  @apply bg-brand-primary; }
  .toggle_input:checked + .toggle .switch_btn {
    left: calc(100% - 2px);
    transform: translateX(-100%); }

.toggle_input:checked + .toggle_dark {
  @apply bg-content-dark; }

.toggle:active .switch_btn {
  width: 25px; }

/* item group */
.item-group_btn {
  height: 56px;
  border-radius: 8px;
  border: 1px solid theme("colors.tertiary");
  box-shadow: 0px 2px 4px rgba(6, 115, 123, 0.04);
  padding: 0 25px;
  @apply flex items-center justify-center btn-white field cursor-pointer; }

.group_input:checked + .item-group_btn {
  border: 2px solid theme("colors.content.dark");
  @apply px-24px; }

/* tabbar */
.tabs {
  @apply bg-content-grey_ultra_light p-1 relative;
  display: grid;
  border: 1px solid theme("colors.tertiary");
  border-radius: 12px; }

.tab {
  @apply bg-content-white px-24px py-2;
  box-shadow: 0px 4px 7px rgba(31, 174, 185, 0.05);
  border-radius: 12px;
  border: 1px solid theme("colors.tertiary"); }

/* datepicker */
.CalendarDay__selected_span {
  background-color: theme("colors.content.grey_ultra_light") !important;
  color: black !important; }
  .CalendarDay__selected_span:hover {
    background-color: theme("colors.content.dark") !important;
    color: theme("colors.content.white") !important;
    border: none !important; }

.CalendarDay__hovered_span:hover,
.CalendarDay__hovered_span {
  background-color: theme("colors.content.grey_ultra_light") !important;
  color: black !important;
  border: none !important; }

.CalendarDay__selected, .CalendarDay__selected:hover {
  background-color: theme("colors.content.dark") !important;
  color: theme("colors.content.white") !important;
  border-radius: 999px !important;
  border: none !important; }

.CalendarDay {
  border: none !important; }

.DayPicker__withBorder {
  border: 1px solid theme("colors.brand.tertiary");
  box-shadow: 0px 4px 7px rgba(31, 174, 185, 0.05);
  border-radius: 12px; }

.DayPickerNavigation_button .icon_arrowLeft {
  left: 30px;
  position: absolute;
  top: 20px;
  margin: 2px 0; }

.DayPickerNavigation_button .icon_arrowRight {
  right: 30px;
  position: absolute;
  top: 20px;
  margin: 2px 0; }

.DateRangePicker_picker {
  font-family: 'Nunito Bold' !important;
  font-size: 12px !important;
  line-height: 20px !important;
  z-index: 50 !important; }

.CalendarMonth_caption > strong {
  font-family: 'Nunito Bold' !important;
  font-size: 12px !important;
  line-height: 20px !important;
  color: theme("colors.text.content.dark") !important; }

.DateInput_input {
  font-family: 'Nunito Black' !important;
  color: theme("colors.text.content.dark") !important;
  letter-spacing: -0.1px !important;
  padding: 1.25rem 1.5rem !important;
  margin-right: 12px !important;
  border: 1px solid theme("colors.tertiary") !important;
  height: auto !important;
  box-shadow: 0px 2px 4px rgba(6, 115, 123, 0.04) !important;
  border-radius: 8px !important;
  font-size: 14px !important;
  line-height: 18px !important;
  width: 160px !important; }

.DateRangePickerInput__withBorder {
  border: none !important; }

.DateRangePickerInput {
  background-color: initial !important; }

.DateRangePickerInput_arrow_svg {
  display: none !important; }

.DateInput {
  width: auto !important;
  background-color: initial !important; }

.icon_startDate {
  @apply absolute iconRound20 bg-content-grey_ultra_light;
  left: 130px;
  top: 51px; }

.icon_endDate {
  @apply absolute iconRound20 bg-content-grey_ultra_light;
  left: 302px;
  top: 51px; }

.DateInput_input__focused {
  box-shadow: inset 0 0 0 2px theme("colors.content.dark") !important; }

.DatePicker_label__right {
  margin-left: 135px; }

.mobile_input {
  @apply box px-6 py-5 mr-12px field w-full;
  font-size: 14px;
  line-height: 18px; }

.datepicker-modal {
  position: fixed !important; }

.datepicker.default .datepicker-navbar-btn {
  @apply field;
  color: theme("colors.content.dark") !important; }

.datepicker.default .datepicker-wheel {
  border-top: 1px solid theme("colors.content.grey_light") !important;
  border-bottom: 1px solid theme("colors.content.grey_light") !important; }

/* dropdown */
.dropdown_select {
  display: none; }

.dropdown {
  box-shadow: 0px 4px 7px rgba(31, 174, 185, 0.05);
  border-radius: 12px;
  border: 1px solid theme("colors.brand.tertiary");
  height: 56px;
  @apply bg-content-white px-24px my-8px flex items-center justify-between text-content-dark cursor-default;
  font-family: "Nunito Black";
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 1; }

.dropdown_active {
  box-shadow: inset 0 0 0 2px theme("colors.content.dark"); }

.dropdown-item {
  @apply info text-content-dark px-24px py-12px;
  border-bottom: 1px solid theme("colors.brand.tertiary"); }
  .dropdown-item:hover {
    @apply bg-content-grey_ultra_light; }
  .dropdown-item:last-child {
    border: none; }

.dropdown-item_active {
  @apply bg-content-grey_ultra_light; }

.dropdown-items-list {
  @apply bg-content-white absolute overflow-y-auto box p-0 mt-8px z-50 cursor-default;
  max-height: 400px; }

/* nav-item */
.navIcon {
  @apply border-1/2 border-opacity-30 iconRound20 text-content-dark
  border-content-grey_light bg-content-white mr-4; }

.navItem:hover .navIcon {
  @apply border-0 bg-content-dark text-content-white;
  transition: color 0.5s, background-color 1s; }

.navIcon_active {
  @apply border-0 bg-content-dark text-content-white; }

/* dropdown menu */
.menu-list {
  width: auto !important;
  left: 15px;
  right: 15px;
  max-height: 80vh; }

@screen xs {
  .menu-list {
    left: 12px;
    right: 12px; } }

.menu {
  border-radius: 55px;
  padding: 12px 16px; }

.menu-item {
  @apply py-0; }

.section {
  box-shadow: 0px 4px 7px rgba(31, 174, 185, 0.05);
  @apply py-3 mark text-content-grey; }

/* table */
.table-wrapper {
  @apply box px-0 overflow-x-auto; }

.table {
  @apply text-left w-full; }
  .table thead {
    @apply mark text-content-dark_light bg-content-white;
    box-shadow: 0px 4px 7px rgba(31, 174, 185, 0.05);
    border-bottom: 1px solid theme("colors.brand.tertiary"); }
  .table tbody {
    @apply H4 bg-content-white;
    font-size: 12px; }
  .table tr th, .table tr td {
    border-right: 1px solid theme("colors.brand.tertiary");
    @apply p-3; }
    .table tr th:first-child, .table tr td:first-child {
      @apply pl-24px; }
    .table tr th:last-child, .table tr td:last-child {
      border: none;
      @apply pr-24px; }

/*slider*/
.input-range__track {
  background: theme("colors.brand.tertiary") !important; }

.input-range__track--active {
  background: theme("colors.brand.primary") !important; }

.input-range__slider {
  border-color: theme("colors.brand.primary") !important;
  background: linear-gradient(179.53deg, #4FCFCF 12.98%, #00ADC5 113.4%) !important;
  height: 20px !important;
  width: 20px !important; }

.input-range__label--value {
  display: none !important; }

.input-range__label-container {
  @apply H4 text-content-grey; }

.input-range__label-container {
  left: 0 !important;
  position: static; }

.input-range__label--max {
  bottom: -2rem !important; }

.input-range__label--min {
  bottom: -2rem !important; }
